import { AuthContext } from "context";
import useAuth from "hooks/useAuth";
import Header from "./Header/Header";
import '../../node_modules/bootstrap/dist/css/bootstrap.css';
import 'bootstrap';
import { BrowserRouter } from "react-router-dom";
import Main from "./Main";
import LoginModal from "./LoginModal";
import * as lsAPI from "lsAPI";
import RegisterModal from "./RegisterModal";
import { useDispatch } from "react-redux";
import { loadUsersFromStorage, setUsers } from "store/slices/usersSlice";
import { loadPostsFromStorage } from "store/slices/postsSlice";

const App = () => {
  const authHook = useAuth();
  const dispatch = useDispatch();

  console.log('Test')
  authHook.loginWithId(lsAPI.getAuthId());

  document.addEventListener('DOMContentLoaded', () => {
    dispatch(loadUsersFromStorage({}));
    dispatch(loadPostsFromStorage({}));
  })

  return (
    <AuthContext.Provider value={authHook}>
      <BrowserRouter basename="/social-media">
        <div className="container-fluid p-0">
          <Header />
          <Main />
          <LoginModal />
          <RegisterModal />
        </div>
      </BrowserRouter>
    </AuthContext.Provider>
  );
}

export default App
