import { AuthContext } from 'context';
import { useFormik } from 'formik';
import pt from 'prop-types'
import { useContext } from 'react';
import { useDispatch } from 'react-redux';
import { createComment } from 'store/slices/postsSlice';
import { v4 } from 'uuid';

const CommentsSection = (props) => {
  const { comments, postId } = props;

  const authContext = useContext(AuthContext);
  const dispatch = useDispatch();
  const form = useFormik({
    initialValues: {
      content: ''
    },
    onSubmit: (v) => {
      const comment = {
        postId,
        userId: authContext.user.id,
        content: v.content
      }
      dispatch(createComment(comment));
    }
  });

  const renderedComments = comments.map(c => (
    <li className="list-group-item" key={v4()}>
      <span style={{ fontWeight: '600' }}>
        {c.authorUsername}
      </span>{'   '}
      {c.content}
    </li>
  ));

  return (
    // <div className="">
    <>

    {/* Form */}
      <form
        onSubmit={form.handleSubmit}
        className="p-2"
      >
        <div className="">
          <input
            className="form-control"
            placeholder="Comment"
            name="content"
            value={form.values.content}
            onChange={form.handleChange}
            disabled={authContext.loggedIn ? false : true}
          />
        </div>
        <div className="mt-1">
          <input
            type="submit"
            className="form-control btn btn-outline-dark"
            value="Leave"
            disabled={authContext.loggedIn ? false : true}
          />
        </div>
      </form>
    {/* Form */}
      
    {/* Comments list */}
      <ul className="list-group">
        {renderedComments}
      </ul>
    {/* Comments list */}{/* </div> */}
    </>
  )
}

CommentsSection.propTypes = {
  comments: pt.array.isRequired,
  postId: pt.string.isRequired
}

export default CommentsSection
