import { AuthContext } from 'context';
import pt from 'prop-types'
import { useContext } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { likePost } from 'store/slices/postsSlice';
import styled from 'styled-components'
import { v4 } from 'uuid';
import CommentsSection from './CommentsSection';

const StyledLikeButton = styled.button`
  white-space: pre;
`;

const Post = (props) => {
  const users = useSelector(state => state.users);
  const dispatch = useDispatch();
  const authContext = useContext(AuthContext);
  
  let { id, authorId, title, content, likes, comments } = props.post;

  const authorUsername = users.find(u => u.id === authorId).username;

  //change comments to pass to CommentsSection
  comments = comments.map(c => {
    return {
      content: c.content,
      authorUsername: users.find(u => u.id === c.userId).username
    };
  });

  const handleLike = () => {
    dispatch(likePost({ userId: authContext.user.id, postId: id }));
  };

  // check if current user liked this post
  // first check if user is logged in
  let likedByUser;
  if (authContext.loggedIn && authContext.user) {

    // and finally check weather user liked this post or not
    if (likes.includes(authContext.user.id)) {
      likedByUser = true;
    } else {
      likedByUser = false;
    }

  } else {
    likedByUser = false;
  }

  const likeButton = likedByUser ? (
    <StyledLikeButton
      className={`btn btn-outline-success`}
      onClick={handleLike}
      disabled={authContext.loggedIn ? true : false}
    >
      {likes.length}  |  Liked
    </StyledLikeButton>
  ) : (
    <StyledLikeButton
      className={`btn btn-outline-danger`}
      onClick={handleLike}
      disabled={authContext.loggedIn ? false : true}
    >
      {likes.length}  |  Like
    </StyledLikeButton>
  );

  return (
    <li className="row">
      <div className="mt-4 card col-12 ffset-2 p-0 border border-dark">

        <div className="card-header">
          <Link to={`/users/${authorUsername}`}>
            {authorUsername}
          </Link>
        </div>

        <div className="card-body">
          <h4 className="card-title">
            {title}
          </h4>
          <p className="card-text">
            {content}
          </p>
        </div>

        <div className="card-footer text-end">
          {likeButton}{' '}
          <a
            href={`#a${id}`}
            data-bs-toggle="collapse"
            className="btn btn-outline-primary"
          >
            Comments
          </a>
        </div>

        <div className="collapse" id={`a${id}`}>
          <CommentsSection
            comments={comments}
            postId={id}
          />
        </div>

      </div>

    </li>
  )
}

Post.propTypes = {
  post: pt.object.isRequired
}

export default Post
