import { AuthContext } from "context";
import { useFormik } from "formik"
import { useContext } from "react";
import { useDispatch } from "react-redux";
import { createPost } from "store/slices/postsSlice";
import { v4 } from "uuid";

const CreatePostForm = () => {
  const authContext = useContext(AuthContext);
  const dispatch = useDispatch();
  const form = useFormik({
    initialValues: {
      title: '',
      content: ''
    },
    onSubmit: (v) => {
      const { title, content } = v;
      if (!authContext.loggedIn) {
        return;
      }
      if (v.title.trim() === '') {
        return;
      }
      if (v.content.trim() === '') {
        return;
      }
      const id = v4();
      const authorId = authContext.user.id;
      dispatch(createPost({ post: {id, authorId, title, content, likes: []} }));
    }
  });

  return (
    <form className="container-fluid" onSubmit={form.handleSubmit}>
      <div className="row">
        <input
          className="form-control"
          placeholder="Title"
          name="title"
          value={form.values.title}
          onChange={form.handleChange}
          disabled={authContext.loggedIn ? false : true}
        />
      </div>
      <div className="row mt-1">
        <textarea
          className="form-control"
          name="content"
          placeholder="Content"
          value={form.values.content}
          onChange={form.handleChange}
          disabled={authContext.loggedIn ? false : true}
        ></textarea>
      </div>
      <div className="row mt-1">
        <button
          type="submit"
          className="btn btn-outline-dark"
          disabled={authContext.loggedIn ? false : true}
        >
          Creat post
        </button>
      </div>
    </form>
  )
}

export default CreatePostForm
