import { v4 } from "uuid";
import * as lsAPI from 'lsAPI'

const { createSlice } = require("@reduxjs/toolkit");

const usersSlice = createSlice({

  name: 'users',

  initialState: [
    // {
    //   id: '0f87601a-267e-48f0-8bea-62fadc34c787',
    //   username: 'SaidovDaler',
    //   password: '2000909k'
    // },
    // {
    //   id: 'f0d32257-8629-4118-983d-b82398cc46e6',
    //   username: 'RasulovAziz',
    //   password: '1000909k'
    // }
  ],

  reducers: {

    loadUsersFromStorage: (state, action) => {
      const users = lsAPI.getUsers();
      state.splice(0, state.length);
      for (let el of users) {
        state.push(el);
      }
    },

    createUser: (state, action) => {
      const { username, password } = action.payload.user;
      const user = { username, password, id: v4() };
      state.push(user);
      lsAPI.setUsers(state);
    },

    deleteUser: (state, action) => {
      const { id } = action.payload;

      state.splice(state.findIndex(user => user.id === id), 1);
    },

    setUsers: (state, action) => {
      const { users } = action.payload;
      console.log(users)
      // clean state
      state.splice(0, state.length);
      // add elements from users to state
      for (let el in users) {
        state.push(el)
      }
    }

  }
  
});

export const { createUser, setUsers, loadUsersFromStorage } = usersSlice.actions;

export default usersSlice.reducer;
