import { useEffect } from "react";
import { useState } from "react";
import * as lsAPI from 'lsAPI';
import { useSelector } from "react-redux";


// gets user from redux store, return user if found, false otherwise
function getUserWithUsernameAndPassword(credentials, users) {
  const { username, password } = credentials;
  const user = users.find(user => {
    if (user.username === username && user.password === password) {
      return true;
    }
    return false;
  });

  if (user) {
    return user;    
  }
  return false;
}

function useAuth() {
  const users = useSelector(state => state.users);
  const [loggedIn, setLoggedIn] = useState(null);
  const [user, setUser] = useState(null);

  useEffect(() => {
    // console.log(loggedIn ? 'Logged in' : 'Logged out');
  }, [loggedIn]);

  // returns true if logged in successfully, false otherwise
  const login = (credentials) => {
    const u = getUserWithUsernameAndPassword(credentials, users);
    if (u) {
      setLoggedIn(true);
      setUser(u);
      lsAPI.setAuthId(u.id);
      return true;
    } else {
      return false;
    }
  };

  const loginWithId = (id) => {
    setTimeout(() => {
      if (id) {
        const foundUser = users.find(u => u.id === id);
        if (foundUser) {
          setLoggedIn(true);
          setUser(foundUser);
        }
      }
    }, 0);
  };

  // logout from system
  const logout = () => {
    setLoggedIn(false);
    setUser(null);
    lsAPI.setAuthId(null);
  };

  return { loggedIn, user, login, loginWithId, logout };
}

export default useAuth;
