const ls = localStorage;

(function init() {
  const users = ls.getItem('users');
  const posts = ls.getItem('posts');

  if (users === 'undefined' || users === null) {
    ls.setItem('users', JSON.stringify([]));
  }

  if (posts === 'undefined' || posts === null) {
    ls.setItem('posts', JSON.stringify([]));
  }
})();

const getAuthId = () => {
  const id = ls.getItem('auth');

  if (id === 'undefined' || id === null) {
    return false;
  }

  return id;
};

const setAuthId = (id) => {
  ls.setItem('auth', id);
};

const getUsers = () => {
  const users = ls.getItem('users');

  return JSON.parse(users);
};

const setUsers = (users) => {
  ls.setItem('users', JSON.stringify(users));
};

const getPosts = () => {
  const posts = ls.getItem('posts');

  return JSON.parse(posts);
};

const setPosts = (posts) => {
  ls.setItem('posts', JSON.stringify(posts));
};

export { setAuthId, getAuthId, getPosts, getUsers, setUsers, setPosts };
