import Post from "./Post"
import pt from 'prop-types'
import { v4 } from "uuid";

const Posts = (props) => {
  const renderedPosts = props.posts.map(post => (
    <Post post={post} key={v4()} />
  ));

  return (
    <ul className="container-fluid">
      {renderedPosts}
    </ul>
  )
}

Posts.propTypes = {
  posts: pt.array
}

export default Posts
