import { AuthContext } from "context";
import { useContext } from "react";
import NavLink from "./NavLink"

const Nav = () => {
  const authContext = useContext(AuthContext);

  const handleLogout = () => authContext.logout();

  const loginLogoutBtn = !authContext.loggedIn ? (
    <button
      type="button"
      className="btn btn-outline-success"
      data-bs-toggle="modal"
      data-bs-target="#modalLogin"
    >
      Login
    </button>

  ) : (
    <button
      className="btn btn-outline-danger"
      onClick={handleLogout}
    >
      Logout
    </button>
  );

  return (
    <nav className="navbar-expand-sm p-2 navbar navbar-expand-sm navbar-dark bg-dark d-flex justify-content-between">

      <button
        className="navbar-toggler" 
        type="button" 
        data-bs-toggle="collapse" 
        data-bs-target="#navbar"
      >
        <span className="navbar-toggler-icon"></span>
      </button>

      <div className="d-flex align-items-center">

        <div className="navbar-brand">
          <h3>SocialMedia</h3>
        </div>

        <div className="collapse navbar-collapse" id="navbar">
          <ul className="navbar-nav">
            <NavLink to="/home">
              Home
            </NavLink>
            <NavLink to="/users">
              Users
            </NavLink>
          </ul>
        </div>

      </div>

      <div className="form-inline mr-2">
        {loginLogoutBtn} {' '}
        <button
          className="btn btn-outline-primary"
          data-bs-toggle="modal"
          data-bs-target="#modal-register"
        >
          Register
        </button>
      </div>

    </nav>
  )
}

export default Nav
