import { createSlice } from '@reduxjs/toolkit';
import * as lsAPI from 'lsAPI'

const postsSlice = createSlice({

  name: 'posts',

  initialState: [
    // {
    //   id: '111',
    //   authorId: '0f87601a-267e-48f0-8bea-62fadc34c787', // Daler
    //   title: 'My life',
    //   content: 'I was born in Bukhara\n, I love that cityI was born in Bukhara\n, I love that cityI was born in Bukhara\n, I love that city',
    //   likes: ['0f87601a-267e-48f0-8bea-62fadc34c787'],
    //   comments: [
    //     {
    //       userId: 'f0d32257-8629-4118-983d-b82398cc46e6',
    //       content: 'Good Post!'
    //     },
    //     {
    //       userId: '0f87601a-267e-48f0-8bea-62fadc34c787',
    //       content: 'Hello!'
    //     },
    //   ]
    // },
    // {
    //   id: '222',
    //   authorId: '0f87601a-267e-48f0-8bea-62fadc34c787', // Daler
    //   title: 'Title 2',
    //   content: 'Content 2',
    //   likes: [],
    //   comments: []
    // },
    // {
    //   id: '333',
    //   authorId: 'f0d32257-8629-4118-983d-b82398cc46e6', // Aziz
    //   title: 'Title 3',
    //   content: 'Content 3',
    //   likes: [],
    //   comments: []
    // }
  ],

  reducers: {

    createPost: (state, action) => {
      const { post } = action.payload;

      // just in case
      post.likes = []
      post.comments = [];

      state.push(post);
      lsAPI.setPosts(state);
    },

    loadPostsFromStorage: (state, action) => {
      state.splice(0, state.length);
      const posts = lsAPI.getPosts();

      for (let post of posts) {
        state.push(post);
      }
    },

    deletePost: (state, action) => {
      
    },

    updatePost: (state, action) => {
      
    },

    likePost: (state, action) => {
      const { userId, postId } = action.payload;

      // ref to likes of post that is liked
      const likes = state[state.findIndex(el => el.id === postId)].likes;

      // if user already liked this post, unlike this post
      if (likes.includes(userId)) {
        likes.splice(likes.indexOf(userId), 1);
        return;
      }

      likes.push(userId);
    },

    createComment: (state, action) => {
      const { postId, userId, content } = action.payload;

      const comments = state[state.findIndex(el => el.id === postId)].comments;

      comments.push({ userId, content })
    }

  }
  
});

export const { createPost, loadPostsFromStorage, likePost, createComment } = postsSlice.actions;

export default postsSlice.reducer;
